@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
html {
  scroll-behavior: smooth !important;
}
body {
  margin: 0;
  /* height: 100vh; */
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: #000;
}
::-webkit-scrollbar-thumb {
  height: 5%;
  background: #f7d919;
  border-radius: 10px;
}

.body {
  min-height: 100vh;
  position: relative;
  font-family: "Inter", sans-serif;
  overflow-x: hidden;
}
main {
  min-height: 100vh;
}
.parent {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  pointer-events: none;
}
.animation-box {
  min-height: 100vh;
  background-color: #1d2a45;
  color: #f7d919;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about {
  min-height: 100vh;
}
.about-header {
  min-height: 100vh;
  background-color: #020101;
  color: #fff;
  background-image: url(/static/media/about-us-hero-bg.c8f83175.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: grid;
  place-items: center;
  list-style: 1.6;
}
.about-header h1 {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
}
.heading-about-primary {
  font-size: 60px;
}
.heading-about-secondary {
  font-size: 35px;
  font-weight: 400;
}
.about-team {
  color: #1d2a45;
}

/* ------------------------------- MEDIA QUERY ------------------------------ */
@media (max-width: 668px) {
  .heading-about-primary {
    font-size: 30px;
  }
  .heading-about-secondary {
    font-size: 20px;
  }
}

.team-member {
  text-align: center;
}
.img-box {
  height: 350px;
  overflow: hidden;
  border-radius: 3px;
  position: relative;
}
.team-member img {
  display: inline-block;
  margin: 0 auto;
  height: 350px;
  border-radius: 3px;
  object-fit: cover;
  transition: all 0.5s ease-in-out;
  cursor: progress;
  position: relative;
}
.team-member p {
  font-size: 14px;
}
.team-member:hover img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-filter: brightness(0.6);
          filter: brightness(0.6);
}
.hide-email {
  position: absolute;
  top: 120%;
  left: 50%;
  width: 100%;
  font-size: 12px !important;
  padding: 4px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  color: #eeeeee;
  background-color: #1d2a45;
  border-bottom: 0.5px solid #fff;
  transition: all 0.8s ease;
  visibility: none;
}
.img-box:hover .hide-email {
  top: 75%;
  visibility: visible;
}
.team-member h6 {
  font-weight: 600;
}

.contact {
  background-image: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.75),
      rgba(0, 0, 0, 0.75)
    ),
    url(/static/media/banner.83009f42.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.contact-heading-box {
  text-align: center;
  padding: 60px 0 40px;
  color: #fff;
}
.contact-heading-box p {
  font-size: 14px;
  width: 50%;
  margin: 0 auto;
}
@media (max-width: 668px) {
  .contact-heading-box {
    padding: 80px 0 40px;
  }
  .contact-heading-box p {
    width: 80%;
  }
  .sm-mb {
    margin-bottom: 10px;
  }
}

.all-courses {
  padding: 2% 5% 3%;
}
.all-courses h1 {
  color: #1d2a45;
  font-weight: 600;
}
@media (max-width: 668px) {
  .all-courses {
    padding: 5%;
  }
}

.course {
  height: 430px;
}
.course img {
  height: 200px;
  object-fit: cover;
  object-position: center;
}
.course-category,
.course-instructor,
.course-duration,
.course-price,
.course-price-free,
.rating,
.users-icon {
  font-size: 14px;
}
.course-level {
  background-color: #e7e8ff;
  color: #8086fe;
  width: -webkit-max-content;
  width: max-content;
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 12px;
}

.course-category {
  color: #0071dc;
}
.course-title {
  font-size: 16px;
}
.course-duration {
  background-color: #ffe4e8;
  color: #ff4a60;
  width: -webkit-max-content;
  width: max-content;
  padding: 5px 12px;
  border-radius: 20px;
  font-weight: 600;
}
.course-price,
.course-price-free {
  font-weight: 600;
  width: -webkit-max-content;
  width: max-content;
  padding: 6px 18px;
  border-radius: 30px;
}
.course-price {
  background-color: #f7c04d;
  color: #000;
}
.course-price-free {
  background-color: #6cbd7e;
  color: #fff;
}
.rating {
  color: #f7c04d;
}
.users-icon {
  color: #0071dc;
}

footer {
  position: relative;
  bottom: 0;
  background-color: #1d2a45;
  color: #fff;
  width: 100%;
  padding: 4% 6% 2%;
}
.footer-list {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px;
  margin: 0;
  padding: 0;
}
.footer-item {
  line-height: 1.8;
}
.footer-item img {
  margin-bottom: 5%;
}
.footer-item h4 {
  font-size: 18px;
  margin-bottom: 5%;
}
.footer-item p {
  color: #eeeeee;
  font-size: 14px;
  font-weight: 300;
}
.copyright-text {
  margin-top: 2%;
  font-weight: 300;
  text-align: center;
  font-size: 14px;
  letter-spacing: 1px;
}
@media (max-width: 668px) {
  footer {
    padding: 15% 10% 5%;
  }
  .footer-list {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
  }
  .copyright-text {
    text-align: left;
    width: 75%;
    font-size: 12px;
    font-weight: 200;
  }
}

.navbar-brand img {
  width: 80%;
}

.header-home {
  position: relative;
  background-image: linear-gradient(
      to right,
      rgba(29, 42, 68, 0.8),
      rgba(29, 42, 68, 0.8)
    ),
    url(/static/media/banner-2.a1ebac3c.jpg);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  min-height: 100vh;
  color: #fff;
}
.header-info {
  position: absolute;
  top: 45%;
  left: 30%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  line-height: 1.6;
}
.header-info h1 {
  font-size: 50px;
  font-weight: 700;
}
.header-info p {
  font-weight: 300;
  letter-spacing: 1px;
}

.courses-home h2 {
  text-align: center;
  font-weight: 600;
  color: #1d2a45;
  padding: 0 !important;
}
.line {
  display: block;
  margin: -15px auto 30px;
  border: none;
  border-bottom: 2px solid #1d2a45;
  width: 250px;
  opacity: 1;
}
.alert-home {
  background-color: #f7d919;
  color: #1d2a45;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.alert-home h2 {
  font-weight: 600;
}
.full-width {
  width: 50% !important;
}

@media (max-width: 668px) {
  .header-info {
    top: 50%;
    left: 50%;
  }
  .header-info h1 {
    font-size: 30px;
    line-height: 1.5;
  }
  .header-info p {
    font-weight: 300;
    letter-spacing: 1px;
  }
  .header-info button {
    font-size: 14px;
  }
  .alert-home h2 {
    font-size: 20px;
    font-weight: 600;
    width: 80%;
  }

  .full-width {
    width: 80% !important;
  }
}

.popular-course {
  height: 500px;
}
.popular-course img {
  height: 250px;
  object-fit: cover;
  object-position: center;
}
.popular-course-level {
  background-color: #e7e8ff;
  color: #8086fe;
  width: -webkit-max-content;
  width: max-content;
  padding: 3px 8px;
  border-radius: 2px;
  font-size: 12px;
}
.popular-course-category {
  color: #0071dc;
}
.popular-course-price,
.popular-course-price-free {
  font-size: 18px;
  font-weight: 600;
  width: -webkit-max-content;
  width: max-content;
  padding: 6px 18px;
  border-radius: 3px;
}
.popular-course-price {
  background-color: #f7c04d;
  color: #000;
}
.popular-course-price-free {
  background-color: #6cbd7e;
  color: #fff;
}
.star-icon {
  color: #f7c04d;
}
.users-icon {
  color: #0071dc;
}

.error-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5%;
  line-height: 2;
}
.error-page img {
  width: 40%;
}

